
.donate-wrap {
  padding: 10px 0;
  margin: 20px auto;
  width: 90%;
  text-align: center;
}
.donate-wrap .donate-btn {
    cursor: pointer;
    border: 0;
    outline: 0;
    border-radius: 100%;
    padding: 0;
    margin: 0;
    letter-spacing: normal;
    text-transform: none;
    text-indent: 0px;
    text-shadow: none;
    background: none;
}
.donate-wrap .donate-btn span {
      display: inline-block;
      width: 80px;
      height: 35px;
      line-height: 35px;
      border-radius: 5px;
      color: #fff;
      font-weight: 400;
      font-style: normal;
      font-variant: normal;
      font-stretch: normal;
      font-size: 18px;
      font-family: "Microsoft Yahei";
      background: #f44336;
}
.donate-wrap .donate-btn span:hover {
        background: #f7877f;
}
.donate-wrap .qr-container {
    padding-top: 20px;
}
.donate-wrap .qr-container a {
      border: 0;
}
.donate-wrap .qr-container img {
      width: 180px;
      max-width: 100%;
      display: inline-block;
      margin: 0.8em 2em 0 2em;
}
.donate-wrap .qr-container .qr-block {
      display: inline-block;
}
.donate-wrap .qr-container .qr-block:hover p {
        animation: roll 0.1s infinite linear;
        -webkit-animation: roll 0.1s infinite linear;
        -moz-animation: roll 0.1s infinite linear;
}
@-webkit-keyframes roll {
from {
    -webkit-transform: rotateZ(30deg);
    transform: rotateZ(30deg);
}
to {
    -webkit-transform: rotateZ(-30deg);
    transform: rotateZ(-30deg);
}
}
@keyframes roll {
from {
    -webkit-transform: rotateZ(30deg);
    transform: rotateZ(30deg);
}
to {
    -webkit-transform: rotateZ(-30deg);
    transform: rotateZ(-30deg);
}
}



