
html,
body {
  margin: 0;
  padding: 0;
}
body {
  font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
  line-height: 1.4em;
  background: #f5f5f5;
  color: #4d4d4d;
  min-width: 230px;
  margin: 0 auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 300;
}
:focus {
  outline: 0;
}
.hidden {
  display: none;
}
.header {
  background: #fff;
  min-width: 320px;
  max-width: 550px;
  margin: 130px auto 40px auto;
  position: relative;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 25px 50px 0 rgba(0, 0, 0, 0.1);
}
.header .title {
    position: absolute;
    top: -155px;
    width: 100%;
    font-size: 100px;
    font-weight: 100;
    text-align: center;
    color: rgba(175, 47, 47, 0.15);
    text-transform: capitalize;
    text-rendering: optimizeLegibility;
}
@media (min-width: 480px) and (max-width: 640px) {
.header .title {
        font-size: 70px;
        top: -140px;
}
}
@media (min-width: 320px) and (max-width: 479px) {
.header .title {
        font-size: 60px;
        top: -130px;
}
}
.header .search-cmd {
    position: relative;
    margin: 0;
    width: 100%;
    font-size: 24px;
    font-family: inherit;
    font-weight: inherit;
    line-height: 1.4em;
    color: inherit;
    padding: 16px 16px 16px 60px;
    border: none;
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: rgba(0, 0, 0, 0.003);
    box-shadow: inset 0 -2px 1px rgba(0, 0, 0, 0.03);
}
.header .search-cmd::-webkit-input-placeholder {
      font-style: italic;
      font-weight: 300;
      color: #e6e6e6;
}
.header .search-cmd:-ms-input-placeholder {
      font-style: italic;
      font-weight: 300;
      color: #e6e6e6;
}
.header .search-cmd::placeholder {
      font-style: italic;
      font-weight: 300;
      color: #e6e6e6;
}
.command {
  width: 80%;
  margin: auto;
  position: relative;
  z-index: 2;
  border-top: 1px solid #e6e6e6;
}
@media (max-width: 640px) {
.command {
      width: 100%;
}
}
.footer {
  color: #777;
  padding: 10px 15px;
  height: 20px;
  text-align: center;
  border-top: 1px solid #e6e6e6;
}
.footer:before {
    content: '';
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 50px;
    overflow: hidden;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2), 0 8px 0 -3px #f6f6f6, 0 9px 1px -3px rgba(0, 0, 0, 0.2), 0 16px 0 -6px #f6f6f6, 0 17px 2px -6px rgba(0, 0, 0, 0.2);
}
.cmd-count {
  float: left;
  text-align: left;
}
.cmd-count strong {
    font-weight: 300;
}
.filters {
  margin: 0;
  padding: 0;
  list-style: none;
  position: absolute;
  right: 0;
  left: 0;
}
.filters li {
    display: inline;
}
.filters li a {
      color: inherit;
      margin: 3px;
      padding: 3px 7px;
      text-decoration: none;
      border: 1px solid transparent;
      border-radius: 3px;
}
.filters li a:hover {
        border-color: rgba(175, 47, 47, 0.1);
}
.filters li a.selected {
        border-color: rgba(175, 47, 47, 0.2);
}
.info {
  margin: 65px auto 0;
  color: #bfbfbf;
  font-size: 10px;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
  text-align: center;
}
.info p {
    line-height: 1;
}
.info a {
    color: inherit;
    text-decoration: none;
    font-weight: 400;
}
.info a:hover {
      text-decoration: underline;
}
@media (max-width: 430px) {
.footer {
    height: 50px;
}
.filters {
    bottom: 10px;
}
}



