
.card {
  display: inline-block;
  width: 200px;
  height: 150px;
  margin: 5px;
  color: #f2f2f2;
  font-weight: bold;
  text-align: center;
  font-size: 18px;
  box-sizing: border-box;
  padding-top: 70px;
  position: relative;
  transition: all 200ms ease;
}
.card:hover {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    cursor: pointer;
    box-shadow: 0 3px 10px rgba(0, 0, 0, 0.23), 0 3px 10px rgba(0, 0, 0, 0.16);
}
.card a {
    position: absolute;
    top: 0;
    left: 0;
    background: none;
    width: 100%;
    height: 100%;
}



